.regList{
    margin-top: calc(5px + 3vmin);
    background-color: #006600;
    height: calc(355px + 6vmin);
    width: calc(200px + 40vmin);
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-flow: column wrap;
    border-radius: 7px;
}

h2{
    text-align: center;
    color: #ffd700;
    font-size: calc(1px + 3.5vmin);
    margin-top: 20px;
    margin-bottom: 30px;
    text-shadow: grey 1px 1px;
}

input{
    margin-bottom: 25px;
    margin-left: 40px;
    margin-right: 40px;
    border: 0px;
    border-radius: 3px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    outline: none;
}

button{
    margin-bottom: 30px;
    margin-left: 40px;
    margin-right: 40px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: #ffd700;
    border: solid #dfc11a;
    border-radius: 5px;
    color: #282c34;
}

button:hover{
    background-color: #282c34;
    color: whitesmoke;
    border: solid whitesmoke;
}

p{
    font-size: calc(6px + 1.5vmin);
    margin: 0px;
}

.textInput{
    font-size: 18px;
}

.imageBut{
    border-radius: 5px;
    align-self: center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}