.convBlock{
    margin-top: calc(5px + 3vmin);
    background-color: #006600;
    height: calc(150px + 6vmin);
    width: calc(200px + 42vmin);
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-flow: column wrap;
    border-radius: 7px;
}

h2{
    text-align: center;
    color: #ffd700;
    font-size: calc(1px + 3.5vmin);
    margin-top: 20px;
    margin-bottom: 30px;
    text-shadow: grey 1px 1px;
}

h4{
    margin: 0;
    color: #ffd700;
    font-size: calc(4px + 1.5vmin);
    text-shadow: grey 1px 1px;
}

select{
    width: calc(105px + 7.5vmin);
}

.inputBlock{
    display: flex;
    justify-content: space-around;
}

.numInp{
    margin-top: 4px;
    width: calc(60px + 7.5vmin);
    text-align: center;
}

.valBlock{
    display: flex;
    justify-content: space-around;
}