.moneyBlock{
    background-color: #006600;
    border-radius: 7px;
    height: 35%;
    align-items: center;
    margin-bottom: 15px;
}

.text{
    color: white;
    text-shadow: gold 0.5px 0.5px;
    text-align: center;
    padding: calc(0.5px + 1.5vmin);
    font-size: calc(0.5px + 3vmin);
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

ul{
    padding-left: 100px;
    padding-right: 100px;
}