.App {
  text-align: center;
  display: flex;
  flex-flow: column wrap;
}

.App-logo {
  height: 25vmin;
  pointer-events: none;
  padding-top: 0.01%;
  padding-left: 5%;
}

.headerBlock{
  background-color: #006600;
  display: flex;
  justify-content: space-around;
  box-shadow: gold 0px 0px 3px; 
}

.upBlock{
  display: flex;
}

h3{
  font-size: calc(0.5px + 2vmin);
  border: solid gold 3px;
  padding: 7px;
  border-radius: 7px;
}

h1{
  font-size: calc(0.5px + 5vmin);
  text-shadow: grey 1px 2px;
  padding-top: calc(0.5px + 3vmin);
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 1s linear  alternate;
  }
}

@keyframes App-logo-spin {
  from {
    transform: translate(0px, 0px);
  }
  to {
    transform: translate(5%, 0px);
  }
}
